import React from 'react';
import CM from './ClientManager';


class Chat extends React.Component<{ user: string }, { message: string; chatMsgs: string }>{
    constructor(props: any) {
        super(props);
        this.state = {
            message: "",
            chatMsgs: ""
        }
    }
    componentDidMount() {
        CM.initChat(this.addMsg);
    }
    sendMsg = (event: any) => {
        event.preventDefault();
        const { user } = this.props;
        const { message } = this.state;       
        CM.emitMsg(user, message);
        this.setState({
            message: ""
        })
    }

    addMsg = (msg: string) => {
        this.setState({
            chatMsgs: this.state.chatMsgs + "\n" + msg
        })
    }

    handleChange = (event: any) => {
        const {value} = event.target;
        this.setState({
            message: value
        })
    }


    render = () => {
        const { message, chatMsgs } = this.state;
        return (
            <div className="chatContainer"  >
                <textarea readOnly={true} value={chatMsgs}></textarea>
                <div className='chatInputs'>
                <form onSubmit={this.sendMsg}>
                <input type='text' id='msg' value={message} onChange={this.handleChange} placeholder='enter your message here...' autoComplete='off'></input>
                <button>Send</button>
                </form>
                </div>
            </div>
        )
    }
}

export default Chat